.solvingTest-container{
    background: var(--background);
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
}
.progress{
    padding: 50px;
    padding-left: 100px;
    padding-right: 100px;
    stroke-color: var(--nav-color);
    trailColor: var(--answer-dot);
}